import axios from 'axios'
export default {
  name: 'paceMissionDetail',
  props: {
    mission: Object
  },
  data () {
    return {
      gainList: [],
      startTime: sessionStorage.getItem('missionStartTime'),
      endTime: sessionStorage.getItem('missionEndTime')
    }
  },
  methods: {
    getGainList () {
      axios.get(this.url + '/gain/mission/' + sessionStorage.getItem('missionID'))
        .then(this.getGainListSucc)
    },
    getGainListSucc (res) {
      if (res.status === 200) {
        res = res.data
        this.gainList = res
      }
    }
  },
  mounted () {
    this.getGainList()
  }
}



